
.filter {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  padding: 10px 0 0 28px;
  font-size: 12px;
  line-height: 11px;
  color: #fff;
  z-index: 9999;
}
.filter .myCalendar {
  left: auto !important;
}
.filter input {
  background: transparent;
  border: none;
  color: #fff;
}
.filter .timeText {
  opacity: 0.69;
  padding-right: 7px;
}
.filter .startTime {
  display: inline-block;
}
.filter .endTime {
  display: inline-block;
  padding-left: 42px;
}
.filter .el-date-editor {
  width: 55%;
}
.filter .products {
  position: absolute;
  display: inline-block;
  right: 15px;
}
.filter .products .all {
  display: inline-block;
  margin-right: 18px;
}
.filter .products .all .v-checkbox {
  position: relative;
  top: 2px;
  left: -3px;
}
.filter .products .pro {
  float: right;
  padding: 2px 25px 2px 2px;
}
.filter .products .pro .arrow {
  position: absolute;
  width: 6px;
  height: 6px;
  margin-left: 10px;
  border-left: 2px solid #fff;
  border-bottom: 2px solid #fff;
  transform: rotate(-45deg);
}
.filter .pro_list {
  position: absolute;
  right: 22px;
  width: 102px;
  text-align: left;
  background: #1e3642;
  font-size: 14px;
  margin-top: 22px;
/*max-height 180px*/
  overflow: hidden;
  z-index: 9;
}
.filter .pro_list li {
  height: 36px;
  line-height: 36px;
  border-bottom: 1px solid rgba(255,255,255,0.1);
}
.filter .pro_list li:last-child {
  border: none;
}
.filter .pro_list li .v-checkbox {
  left: 8px;
}
.filter .pro_list li .name {
  position: absolute;
  display: inline-block;
  right: 8px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 70px;
}


